:root[color-theme='light'] {
  --background: #fff;
  --text: #363636;
  --heading-text: #000;
  --anchor-text: #0076d1;
  --code-background: #efefef;
  --code-text: #000;
}

html {
  background: var(--background);
  color: var(--text);
  a {
    color: var(--text);
  }
}